import { request,getToken,Token } from '../../request.js'
export default {
	data() {
		return {
			old_password:'',
			new_password:'',
		};
	},
	methods: {
		// 修改密码
		reset() {
			var old_password = this.old_password;
			var new_password = this.new_password;
			//验证字符
			request({
				url: "index.php?s=/wap/member/apimodifyPassword",
				method: "POST",
				data: {
					old_password: old_password,
					new_password: new_password
				}
			}).then(res => {
				console.log(res);
				if(res.data.code == 1){
						uni.showToast({
							title: '修改成功',
							icon: 'none'
						});
						
						setTimeout(function(){
							uni.navigateBack({
								delta:1
							});
						}, 1500);
				}
				else {
					uni.showToast({
						title: '用户名与密码不匹配',
						icon: 'none'
					})
				}
			})
		},
		
	}

}
